.section-1{background-image:-webkit-gradient(linear, left top, left bottom, from(#c1e1ee), to(#9ad9e7));background-image:linear-gradient(to bottom, #c1e1ee, #9ad9e7)}.section-2{background-color:white}.section-3{background-color:#c1e1ee;padding-bottom:30px;overflow:hidden}.section-5{background-color:#c1e1ee}

nav p{-webkit-transition:0.15s ease-in;transition:0.15s ease-in}nav p:hover{transform:scale(1.1);-ms-transform:scale(1.1);-webkit-transform:scale(1.1)}nav .navbar-brand{padding-left:20px}nav .nav-item{padding:0.9em}nav .nav-item:last-child{padding-right:5em}nav p{color:#2d8db3;font-size:20px;font-family:"Josefin Sans",sans-serif}@media (max-width: 768px){.navbar{background:#c1e1ee}}

.jumbotron,.jumbotron .container{display:flex;justify-content:center;align-items:center;margin:0px;padding:0px;overflow:hidden}.jumbotron .container{-webkit-transition:0.15s ease-in;transition:0.15s ease-in}.jumbotron .container:hover{transform:scale(1.1);-ms-transform:scale(1.1);-webkit-transform:scale(1.1)}.jumbotron{height:60vh;background:transparent}.jumbotron .container{overflow:visible}.jumbotron img{width:55%;height:55%}@media (max-width: 768px){.jumbotron img{width:80%;height:80%}}@media (max-width: 576px){.jumbotron{height:50vh}.jumbotron img{width:100%;height:100%}}

.container-fluid{display:flex;justify-content:center;align-items:center;margin:0px;padding:0px;overflow:hidden}.container-fluid img{width:40%;height:40%}@media (max-width: 768px){.container-fluid img{width:55%;height:55%}}@media (max-width: 576px){.container-fluid img{width:100%;height:100%}}

.about .left-col,.about .right-col h1{display:flex;justify-content:center;align-items:center;margin:0px;padding:0px;overflow:hidden}.about .left-col{-webkit-transition:0.15s ease-in;transition:0.15s ease-in}.about .left-col:hover{transform:scale(1.1);-ms-transform:scale(1.1);-webkit-transform:scale(1.1)}.about .left-col img{width:60%;height:auto;margin:40px 0px;border-radius:10px}.about .right-col{overflow:hidden}@media (min-width: 768px) and (max-width: 1199px){.about .right-col{padding:30px 0px}}.about .right-col h1{color:#2d8db3;font-size:30px;font-family:"Nunito",sans-serif;font-weight:bold}.about .right-col p{color:#545454;font-size:18px;font-family:"Nunito",sans-serif;margin:20px 0px}

.footer .socialButtons,.footer .copyright{display:flex;justify-content:center;align-items:center;margin:0px;padding:0px;overflow:hidden}.footer .socialButtons .social{margin:10px}.footer .copyright p{text-align:center;color:#9b9b9b}.footer .copyright .heart{color:red}

.social a{display:flex;justify-content:center;align-items:center;margin:0px;padding:0px;overflow:hidden}.social a{width:60px;height:60px;text-decoration:none;font-size:18px;color:#2d8db3}.social a::before{width:60px;height:60px;content:"";position:absolute;background:white;border-radius:50%;border-bottom:3px solid #2d8db3;z-index:-1;-webkit-transition:0.3s ease-in;transition:0.3s ease-in}.social a:hover::before{-webkit-transform:scale(0);transform:scale(0)}.social a i{-webkit-transition:0.3s ease-in;transition:0.3s ease-in}.social a:hover i{background:linear-gradient(45deg, #2d8db3, #2d8db3);background-clip:text;-webkit-background-clip:text;-webkit-text-fill-color:transparent;-webkit-transform:scale(2.2);transform:scale(2.2)}

.breaker img{display:flex;justify-content:center;align-items:center;margin:0px;padding:0px;overflow:hidden}@media (min-width: 1200px){.breaker img{width:30%}}

.skills .skill{margin:10px 0px}

.skill,.skill p{display:flex;justify-content:center;align-items:center;margin:0px;padding:0px;overflow:hidden}.skill{-webkit-transition:0.15s ease-in;transition:0.15s ease-in}.skill:hover{transform:scale(1.1);-ms-transform:scale(1.1);-webkit-transform:scale(1.1)}.skill{width:auto;height:40px;background:white;border-radius:15px;border-bottom:3px solid #2d8db3}.skill p{font-family:"Nunito",sans-serif;font-size:23px;color:#2d8db3}

html{scroll-behavior:smooth}::-webkit-scrollbar-track{border-radius:10px}::-webkit-scrollbar{width:12px}::-webkit-scrollbar-thumb{border-radius:10px;background-image:-webkit-gradient(linear, left bottom, left top, color-stop(0.44, #7a99d9), color-stop(0.72, #497dbd), color-stop(0.86, #2d6ab9))}

